import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			date: '请选择日期',
			dateArr: '',
			bitmap: true,
			navBar: navBar,
			list: [],
			page: 1,
			loading:false
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		detailPage(id) {
			uni.navigateTo({
				url: 'billdetail?id='+id
			});
		},
		openCalendar() {
			this.$refs.calendar.open()
		},
		confirm(e) {
			if (!e.range.before || !e.range.after) {
				rs.Toast("请选择正确的日期区间")
			}else{
				this.date = e.range.before + ',' + e.range.after;
				this.dateArr = e.range.before + ',' + e.range.after;
				this.list = [];
				this.moneyList()
			}
		},
		/**
		 * 账单列表
		 */
		moneyList() {
			var that = this;
			var time = JSON.stringify(that.dateArr);
			if (time == "") {
				time = '';
			} else {
				time = JSON.stringify(that.dateArr);
			}
			var date = time;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
				page: 1,
				date_str: date,
				num: 20,
			}
			that.list = [];
			rs.getRequest("moneyListPaginate", data, (res) => {
				if (res.data.code == 200) {
					if (res.data.data.list != '') {
						that.bitmap = true;
						for(var i=0;i<res.data.data.list.length;i++){
							that.list.push(res.data.data.list[i]);
						}
						if (that.list.length < 20) {
							this.loading = false;
						} else {
							this.loading = true;
						}
					} else {
						this.loading = '空';
						that.bitmap = false;
					}
				} else {
					this.loading = '空';
					rs.Toast(res.data.msg)
				}
			})
		},
	},
	onShow() {
		var that = this;
		that.moneyList()
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {
		var that = this;
		var time = that.dateArr;
		if (time == "请选择日期") {
			time = '';
		} else {
			time = that.dateArr;
		}
		var page = that.page;
		var num = 20;
		var date = time;
		var timeStamp = Math.round(new Date().getTime() / 1000);
		var obj = {
			appid: appid,
			timeStamp: timeStamp,
		}
		var sign = md5.hexMD5(rs.objKeySort(obj) + app.globalData.appsecret);
		var data = {
			appid: appid,
			timeStamp: timeStamp,
			sign: sign,
			page: page + 1,
			date: date,
			num: 20
		}
		rs.getRequest("moneyListPaginate", data, (res) => {
			if (res.data.code == 200) {
				if (res.data.data.moneyList != '') {
					for(var i=0;i<res.data.data.moneyList.length;i++){
						this.list.push(res.data.data.moneyList[i]);
					}
					this.page = page + 1;
					this.loading = true;
				}else{
					this.loading = false;
				}
			}
		})

	},
};
